.profile__illustration-redact {
    position: absolute;
    z-index: 10;
    top: 47px;
    bottom: 47px;
    left: 47px;
    right: 47px;
}

.profile__illustration-redact:hover {
    transform: scale(1.6, 1.6);
}


.profile__illustration-redact:active {
    transform: scale(0.8, 0.8);
}