.profile__title {
max-width: 302px;
margin: 0;
font-style: normal;
font-weight: 500;
font-size: 42px;
line-height: 48px;
color: #FFFFFF;
overflow: hidden;
white-space: nowrap;
text-overflow: ellipsis;
}



@media screen and (max-width: 320px) {
    .profile__title {
        max-width: 190px;
        font-size: 27px;
        line-height: 33px;
        text-align: center;
    }
}